</template>

<script>
import NavBar from '@/components/NavBar.vue';
import RegisterModal from '@/components/RegisterModal.vue';
import CodeModal from '@/components/CodeModal.vue';
import LoginModal from '@/components/LoginModal.vue';
import Toast from '@/components/Toast.vue';
import http from '@/http/index';

import storage from 'good-storage'
import { truncate } from 'fs';

export default {
  name: 'home',
  data() {
    return {
      showCode: false,
      showRegister: false,
      showLogin: false,
      token: null,
      joinBtnText: '报名参会',
      isShowToast: false,
      toastText: '',
      loginData: null,
    }
  },
  mounted() {
    const token = storage.get('token');
    this.token = token ? token : null;
    if(token) {
      this.joinBtnText = '立即参赛'
    }
  },
  methods: {
    toastClose() {
      setTimeout(() => {
          this.isShowToast = false
        }, 2000)
    },
    handleRegisterOption(code) {
      let data = this.loginData;
      let params = Object.assign({}, data, {code: code});
      http.get('/v1api/register', {params}).then(res => res.data).then(res => {
        if(res.errcode === 0) {
          storage.set('token', res.data.USS);
          storage.set('uinfo', res.data.uinfo);
          window.location.reload();
        } else {
          this.toastText = res.errmsg;
          this.isShowToast = true;
        }
      })
    },
    handleJoin() {
      if(this.token) {
        this.$router.push({ path: 'detail'});
      } else {
        this.showLogin = true;
      }
    },
    handleGetCapture(data) {
      this.loginData = data;
      this.showCode = true;
    },
    handleCloseCodeModal() {
      this.showCode = false;
    },
    handleCloseRegisterModal() {
      this.showRegister = false;
    },
    handleLoginSuccess() {
      this.showLogin = false;
    },
    handleCloseLoginModal() {
      this.showLogin = false;
    },
    handleJoinRegister() {
      this.showLogin = false;
      this.showRegister = true;
    }
  },
  components: {
    RegisterModal,
    CodeModal,
    LoginModal,
    NavBar,
    Toast,
  }
}
</script>
