import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
// import Detail from './views/second/Detail.vue';

Vue.use(Router)

export default new Router({
  // mode: 'modal',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/user',
      name: 'user',
      component: () => import('./views/user/View.vue'),
    },
    {
      path: '/detail',
      name: 'detail',
      component: () => import('./views/second/Detail.vue'),
    },
    {
      path: '/challenge',
      name: 'challenge',
      component: () => import('./views/scheme/Challenge.vue'),
    },
    {
      path: '/double',
      name: 'double',
      component: () => import('./views/scheme/Double.vue'),
    },
    {
      path: '/upload',
      name: 'upload',
      component: () => import('./views/upload/Upload.vue'),
    },
    {
      path: '/competition',
      name: 'competition',
      component: () => import('./views/competition/View.vue'),
    }
  ]
})
