<template>
  <div>
    <div class="modal-capture" v-if="isShow">
      <div class="capture-modal">
        <img src="../common/images/close.png" alt="" class="close" @click="handleClose">
        <div class="capture" @click="handleChangeImg">
          <img class="cap" ref="codeImg" src="https://www.caiiic.com/kcaptcha/index.php?PHPSESSID=0pvjpiivevmqb8nk9mcoro0ur0" alt="">
        </div>
        <div class="input">
          <input type="text" placeholder="请输入验证码" class="code" v-model="code">
        </div>
        <div class="submit" @click='handleSubmit'>提交</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      value: false
    },
    status: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      code: ''
    }
  },
  methods: {
    handleSubmit() {
      this.$emit('submit', this.code);
    },
    handleClose() {
      this.$emit('close');
    },
    handleChangeImg() {
      this.$refs.codeImg.src = `https://www.caiiic.com/kcaptcha/index.php?PHPSESSID=0pvjpiivevmqb8nk9mcoro0ur0`
    }
  }
}
</script>
