<script>
  import storage from 'good-storage'

export default {
  name: 'NavBar',
  props: {
    page: {
      type: String,
      default: 'one'
    },
    linkText: {
      type: String,
      default: '上传方案'
    },
    linkType: {
      type: String,
      default: 'competition'
    }
  },
  data() {
    return {
      token: null,
      font: '',
      unifo: {}
    }
  },
  methods: {
    handleLinkPage() {
      this.$router.push('/detail')
      // if(this.linkType === 'competition') {
      //   this.$router.push('/competition');
      // } else {
      // }
    },
    handleLinkUser() {
      this.$router.push('/user');
    },
    handleJoin() {
      this.$emit('login');
    }
  },
  mounted() {
    const token = storage.get('token');
    const uinfo = storage.get('uinfo') ? JSON.parse(storage.get('uinfo')) : '';
    if(token) {
      this.token = token ? token : null;
      this.unifo = uinfo;
      this.font = uinfo.nick.slice(0,1);
    }    
  }
}
