<template>
  <div class="login-modal" v-if="isShow">
    <div class="body">
      <div class="close" @click='handleClose'>
        <img src="../common/images/close.png" alt="">
      </div>
      <div class="content">
        <div class="title">登录</div>
        <div class="mobile">
          <input type="mobile" v-model='nick' placeholder="联系人"/>
        </div>
        <div class="pwd">
          <input type="pwd" v-model='pwd' placeholder="密码"/>
        </div>
        <div class="error" v-if='showErrorMsg'>{{errorMsg}}</div>
        <div class="op-area">
          <div class="left">
            <p>还没有登录账户？</p>
            <div class="join" @click="handleJoinRegister">立即报名参赛</div>
          </div>
          <div class="right">
            <div class="login"  @click='handleLogin'>登录</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import storage from 'good-storage'
import http from '@/http/index';
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      nick: '',
      pwd: '',
      errorMsg: '登录信息不能为空~',
      showErrorMsg: false
    }
  },
  methods: {
    handleLogin() {
      const {nick, pwd} = this;
      if(nick === '' || pwd === '') {
        return this.showErrorMsg = true;
      }
      http.get('/v1api/login', {params: {nick, pwd}}).then(res => res.data).then(res => {
        if(res.errcode === 0) {
          console.log(res.data);
          storage.set('token', res.data.USS);
          storage.set('uinfo', JSON.stringify(res.data.uinfo));
          window.location.reload();
        } else {
          this.errorMsg = red.errmsg;
          this.showErrorMsg = true
        }
      })
    },
    handleClose() {
      this.$emit('close');
    },
    handleJoinRegister() {
      this.$emit('joinRegister');
    }
  }
}
</script>
