<template>
  <transition name="toast">
    <div class="toast" v-show="showFlag">
      <div class="text">{{text}}</div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      text: {
        type: String,
        default: ''
      },
      delay: {
        type: Number,
        default: 2000
      },
      autoClose: {
        type: Boolean,
        default: true
      },
      show: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        showFlag: this.show
      }
    },
    methods: {
      onShow() {
        clearTimeout(this.timer)
        if (this.autoClose) {
          this.timer = setTimeout(() => {
            this.close()
          }, this.delay)
        } else {
          this.showFlag = false
        }
      },
      showToast() {
        this.showFlag = true
        this.onShow()
      },
      close() {
        this.showFlag = false
        this.$emit('close')
      }
    },
    watch: {
      show(val) {
        this.showFlag = val
        this.onShow()
      }
    }
  }
</script>

<style lang="scss">

  .toast{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 400px;
    z-index: 9999;
    border-radius: 5px;
    background: Rgba(0,0,0,0.8);
    height: 45px;
    line-height: 45px;
    &.toast-enter-active, &.toast-leave-active
      {transition: all 0.3s ease}
    &.toast-enter, &.toast-leave-to
      {transform: translate3d(0, -100%, 0)}
    .text{
      color: #fff;
      font-size: 18px;
      text-align: center;
    }
  }
    
</style>
